  .eb-progressbar-box .eb-progressbar-title {
    font-size: 16px;
    font-weight: 400; }

.eb-progressbar-box-fill {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 0;
  background-color: #000;
  -webkit-transition: height 1500ms linear;
  -o-transition: height 1500ms linear;
  transition: height 1500ms linear; }
